/**
 * Creates a new user in the database with the session UUID
 * @returns {Promise<*>} The current user's session UUID, to be stored locally via cookies
 */
async function createUser() {
    const uuid = await calculateSessionUUID();

    try {
        await signInAnonymously(auth);
        await set(ref(database, 'users/' + uuid), {
            lastAccess: Date.now(),
            systemMessages: [],
            userMessages: [],
            teams: []
        });
    } catch (e) {
        console.log(e);
    }

    return uuid;
}

async function calculateSessionUUID() {
    return await getSHA256Hash(
        navigator.userAgent + navigator.language + navigator.platform +
        (new Date().toLocaleString()) + Math.floor(Math.random() * 1000000).toString()
    );
}

export {
    createUser,
    credCallAWS
};